import { useEffect, useState } from "react";
import { HashRouter, Routes, Route, } from "react-router-dom";
import { More } from "./components/More/More";
import { Profile } from "./components/Profile/Profile";
import { Questions } from './components/Question/Questions'
import { useDispatch, } from 'react-redux'
import { requestGetToken, setToken } from './controllers/localstorage.controller'
import { Reply } from "./components/Question/QuestionInput/Reply/Reply";
import { Loader } from "./components/Loader/Loader";
import ApiTelegram from './api.telegram';
import { ShareProfile } from "./components/Profile/ShareProfile/ShareProfile";
import './App.css';

ApiTelegram.init();

function App() {
  const [isLoaded, setIsLoaded] = useState(false)
  const dispatch = useDispatch()

  dispatch({ type: "GET_STATE_WIN_REPLY" });
  dispatch({ type: "GET_STATE_EDIT_PROFILE" });

  const handleRequestToken = async () => {
    try {
      const dataUnsafe = await ApiTelegram.getInitDataUnsafe()

      if (dataUnsafe?.user.id === undefined) {
        throw new Error("Property `id` not defined")
      }

      dispatch({
        type: "SET_TG_DATA", data: {
          userId: dataUnsafe.user.id
        }
      });

      // console.log(dataUnsafe?.user.id)
      const token = await requestGetToken(dataUnsafe.user.id)
      if (token !== null) {
        setToken(token)
        setIsLoaded(true)
      }
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    handleRequestToken()
  }, [])
  //1590124865 //dataUnsafe.user.id //492457018 //1019210352

  if (isLoaded) {
    return (
      <div className="App">
        <HashRouter>
          <Reply />
          <ShareProfile />
          <Routes>
            <Route path="/" index element={<Profile />} />
            <Route path="/questions" element={<Questions />} />
            <Route path="/more" element={<More />} />
          </Routes>
        </HashRouter>
      </div>
    );
  }

  return (
    <div className="App" style={{ alignItems: 'center', justifyContent: 'center' }}>
      <Loader />
    </div>
  )
}

export default App;
