import {ReactComponent as Share} from './icon/share.svg'
import {ReactComponent as Pencel} from './icon/pencel.svg'
import {ReactComponent as Coins} from './icon/coins.svg'
import {ReactComponent as More} from './icon/more.svg'
import './style.css'
import { useState } from 'react';
import { useDispatch } from 'react-redux'

export const HeaderProfile = ({user, itemsLen}) => {
    const dispatch = useDispatch()
    const {firstName, lastName, followers, following, link, photo, answersCount, coins, screenName} = user;
    const [info, setInfo] = useState({
        firstName: firstName || "",
        lastName: lastName || "",
        followers: followers || 0,
        following: following || 0,
        link: link || `http://ama.fan/${screenName}`,
        photo: photo || "",
        answersCount: answersCount || 0, 
        coins: coins || 0
    })

    const avatar = {
        backgroundImage: 'url(' + photo + ')',
        backgroundSize: itemsLen > 0 ? '80px 80px' : '96px 96px',
    }

    const handleOpenWinShare = (sreenname) => {
        dispatch({ type: "SET_STATE_EDIT_PROFILE", data: {
            isOpen: true,
            screenName: sreenname
        }})
    }

    if (itemsLen > 0) {
        return (
            <div>
                <div className='header-profile-coins-and-more'>
                    <div className='header-profile-coins'>
                        <Coins />
                        <span>{coins}</span>
                    </div>
                    {/* <More /> */}
                </div>
                <div className='header-profile-main-container'>
                    <div className='header-profile-info'>
                        <h2>{info.firstName} {info.lastName}</h2>
                    </div>
                    <div className='header-profile-img' style={avatar}>
                        {/* <img src={info.photo} alt="fdf"/> */}
                    </div>
                    <div className='header-profile-counter'>
                        <div className='profile-counter'>
                            <p>{info.answersCount}</p>
                            <span>Ответов</span>
                        </div>
                        <div className='profile-counter'>
                            <p>{followers}</p>
                            <span>Подписчиков</span>
                        </div>
                        <div className='profile-counter'>
                            <p>{following}</p>
                            <span>Подписки</span>
                        </div>
                    </div>
                    <div className='header-prifle-share'>
                        <div className='header-profile-share-link'>{link}</div>
                        {/* <div className='header-profile-share-icon'>
                            <Pencel onClick={handleOpenEditWin}/>  
                        </div> */}
                        <div className='header-profile-share-icon'>
                            <Share onClick={() => handleOpenWinShare(screenName)}/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    const photoMax = {
        backgroundImage: `url("${photo}")`,
    }

    return (
        <div id='main-empty-profile-container'>
            <div id='empty-profile-header-back' style={photoMax}> </div>
            <div id='empty-profile-container'>
                <div id='empty-profile-avatar' style={avatar}></div>
                <div id='empty-profile-text'>
                    <h2>{info.firstName} {info.lastName}</h2>
                </div>
                <div className='header-profile-share-link'>{link}</div>
            </div>
        </div>
    )
}