import axios from "axios";


//* отправка запроса - получение токена авторизации
export const requestGetToken = async (externalUserId) => {
    try {
        const response = await axios.post(`${process.env.REACT_APP_URL_API}/auth/botweb`, {
            externalUserId: externalUserId
        })
        if (response.data?.token === undefined) {
            throw new Error('Property `token` is not found')
        }
        return response.data?.token
    }
    catch (error) {
        console.log(error);
        return null
    }
}

//* установить токен в локальное хранилище
export const setToken = (token) => {
    localStorage.setItem('ACCESS_TOKEN', token)
}

//* получить токен из локального хранилища
export const getToken = () => {
    return localStorage.getItem('ACCESS_TOKEN')
}