import { QuestionMenu } from "./QuestionMenu/QuestionMenu"
import { ReactComponent as InputIcon } from './icon/Input.svg'
import { ReactComponent as OutputIcon } from './icon/Output.svg'
import { QuestionEmpty } from "./QuestionEmpty"
import { useSelector } from 'react-redux'
import { QuestionInput } from "./QuestionInput/QuestionInput"
import { QuestionOutput } from "./QuestionOutput/QestionOutput"
import { BottomMenu } from "../BottomMenu/bottom-menu"
import './style.css'

export const Questions = () => {
    const pageNum = useSelector(state => state.questionMenu.page)

    const InEmptyElem = (
        <QuestionEmpty
            Icon={InputIcon}
            title="Позови друзей!"
            description="Чтобы получить первые вопросы, расскажи друзьям о своём профиле" />
    )
    const OutEmptyElem = (
        <QuestionEmpty
            Icon={OutputIcon}
            title="Задавай вопросы!"
            description="Здесь будут собраны все вопросы, на которые тебе ответят" />
    )

    return (
        <div className="main-page-container">
            <QuestionMenu />
            {pageNum === 1
                ?
                <QuestionInput EmptyElem={InEmptyElem} />
                :
                <QuestionOutput EmptyElem={OutEmptyElem} />
            }
            <BottomMenu active={2} />
        </div>
    )
}