import { QuestionItem } from "./QuestionItem/QestionItem"
import { useDispatch, useSelector } from 'react-redux'
import { getQuestions } from "../../../actions/question"
import { Loader } from "../../Loader/Loader"
import { useEffect } from "react"
import { QuestionEmpty } from "../QuestionEmpty"
import { ReactComponent as MoreQuestions} from '../icon/MoreQuestions.svg'

export const QuestionInput = ({ EmptyElem }) => {
    const dispatch = useDispatch()
    const questionData = useSelector(state => state.questionData.data)

    useEffect(() => {
        getQuestions()
        .then(questions => {
            dispatch({ type: "SET_QUESTIONS", data: questions})
        })
    }, [])
    
    const renderMeQuestion = (EmptyElem, items) => {
        const inputQuestions = items.map((row, index) => {
            return <QuestionItem key={row.id + index} text={row.text} questionId={row.id} index={index} />
        })

        if (items.length > 0 && items.length < 5) {
            return (
                <>
                    {inputQuestions}
                    <div id="more-qeustion-container">
                        <QuestionEmpty 
                            Icon={MoreQuestions} 
                            title={"Хочешь больше вопросов?"} 
                            description={"Периодически рассказывай о своём профиле — рано или поздно друзья придут с новыми вопросами"} />
                    </div>
                </>
            )
        } else if (items.length > 0) {
            return inputQuestions
        }
        return EmptyElem
    }

    if (questionData !== null) {
        return renderMeQuestion(EmptyElem, questionData.inbox.items)
    }

    return (
        <div id="empty-question-contaiter">
            <Loader />
        </div>
    )
}