import { useDispatch, useSelector } from 'react-redux'
import { ReactComponent as Select } from './icon/select.svg'

const colorOne = { color: '#65CC5C' }
const colorTwo = { color: '#71767A' }

export const QuestionMenuButton = ({ pageNum, text }) => {
    const dispatch = useDispatch()
    const pageQuestion = useSelector(state => state.questionMenu.page)

    const handleClickMenuButton = (number) => {
        dispatch({ type: "GO_QESTION_PAGE", page: number })
    }

    return (
        <div className="question-button-menu" onClick={() => handleClickMenuButton(pageNum)}>
            <h2 style={pageQuestion === pageNum ? colorOne : colorTwo}>{text}</h2>
            <Select className={pageQuestion === pageNum ? "question-menu-select-show" : "question-menu-select-hide"} />
        </div>
    )
}