import { questionToggleDelete, userToggleBan } from '../../../../actions/question'
import { ReactComponent as Delete } from './icon/Button.svg'
import { useDispatch } from 'react-redux'
import { useState } from 'react'
import './style.css'
import { Loader } from '../../../Loader/Loader'
export const QuestionItem = ({ text, questionId, index }) => {
    const [isDelete, setIsDelete] = useState(false);
    const [isBan, setIsBan] = useState(false)
    const [isBaning, setIsBaning] = useState(null)
    const dispatch = useDispatch()

    const handleOpenWindowReply = (questionId) => {
        console.log(index)
        dispatch({
            type: "SET_STATE_WIN_REPLY", data: {
                isOpen: true,
                questionId: questionId,
                text: text,
                index: index
            }
        })
    }

    if (!isDelete) {
        return (
            <div className="me-qestion-input-container">
                <div className="me-qestion-input-text">
                    <p>{text}</p>
                    <span>5 минут назад</span>
                </div>
                <div className="me-question-input-action">
                    <button onClick={() => handleOpenWindowReply(questionId)}>Ответить</button>
                    <Delete onClick={() => questionToggleDelete(questionId, setIsDelete, isDelete)}/>
                </div>
            </div>
        )
    }

    const handleLoader = async (questionId, isBan, setIsBan) => {
        setIsBaning(false)
        const result = await userToggleBan(questionId, isBan, setIsBan)
        setIsBaning(result)
    }

    return (
        <div className="me-qestion-input-delete-container">
            <p>Вопрос удалён</p>
            <div className="me-question-input-action-delete">
                <button onClick={() => questionToggleDelete(questionId, setIsDelete, isDelete)}>Восстановить</button>
                <span onClick={() => handleLoader(questionId, isBan, setIsBan)}>
                    {isBan ?  "Разблочить автора" : "Заблочить автора"} 
                </span>
                {isBaning === false && <div id="send-story-loader"><Loader/></div>}
            </div>
        </div>
    )
}