import { QuestionItem } from "./QuestionItem/QestionItem"
import { useSelector, useDispatch } from 'react-redux'
import { getQuestions } from "../../../actions/question"
import { Loader } from "../../Loader/Loader"
import { useEffect } from "react"
import { QuestionEmpty } from "../QuestionEmpty"
import { ReactComponent as MoreQuestions} from '../icon/MoreQuestions.svg'

export const QuestionOutput = ({ EmptyElem, question = {}, isProfile = false }) => {
    const dispatch = useDispatch()
    const questionData = useSelector(state => state.questionData.data)

    useEffect(() => {
        getQuestions()
        .then(questions => {
            dispatch({ type: "SET_QUESTIONS", data: questions})
        })
    }, [])

    const renderMeQuestion = (EmptyElem, items, users) => {
        const outputQuestion = items.map((row, index) => {
            return <QuestionItem key={row.id + index} data={row} users={users} isProfile={isProfile} />
        })

        if (items.length > 0 && items.length < 5 && isProfile) {
            return (
                <>
                    {outputQuestion}
                    <div id="more-qeustion-container">
                        <QuestionEmpty 
                                Icon={MoreQuestions} 
                                title={"Хочешь больше вопросов?"} 
                                description={"Периодически рассказывай о своём профиле — рано или поздно друзья придут с новыми вопросами"} />
                    </div>
                </>
            )
        } else if (items.length > 0) {
            return outputQuestion
        }

        return EmptyElem
    }

    if (questionData !== null) {
        return renderMeQuestion(EmptyElem, question.items || questionData.outbox.items, questionData.users)
    }

    return (
        <div id="empty-question-contaiter">
            <Loader />
        </div>
    )
}