import { ReactComponent as Select } from '../icon/select.svg'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from "react-router-dom";

export const IconButton = ({ element, number, active, pageTo }) => {
    const dispatch = useDispatch()
    const page = useSelector(state => state.menu.page)

    const handleClickMenuButton = (number) => {
        dispatch({ type: "GO_PAGE", page: number })
    }

    return (
        <Link to={pageTo} className={active === number ? 'icon-select-link-active' : 'icon-select-link'}>
            <div className={active === number ? "icon-container-select" : "icon-container"}
                onClick={() => handleClickMenuButton(active)}>
                {element}
                {active === number && <div id="select-share"><Select /></div>}
            </div>
        </Link>
    )
}