export const menuReduser = (state = { page: 1 }, action) => {
    switch (action.type) {
        case "GO_PAGE":
            return { ...state, page: action.page }
        default:
            return state
    }
}

export const menuQuestionReduser = (state = { page: 1 }, action) => {
    switch (action.type) {
        case "GO_QESTION_PAGE":
            return { ...state, page: action.page }
        default:
            return state
    }

}

export const QuestionsData = (state = { data: null }, action) => {
    switch (action.type) {
        case "SET_QUESTIONS":
            return { ...state, data: action.data }
        case "DELETE_QUESTIONS_INPUT":
            return {
                ...state, data: {
                    inbox: { items: state.data.inbox.items.filter(item => item.id !== action.id) },
                    outbox: state.data.outbox,
                    users: state.data.users
                }
            }
        default:
            return state
    }
}

const defaultStateReply = {
    isOpen: false,
    questionId: undefined,
    answer: undefined,
    text: undefined,
    index: undefined,
}

export const QuestionReplyReducer = (state = defaultStateReply, action) => {
    switch (action.type) {
        case "SET_STATE_WIN_REPLY":
            return { ...state, data: action.data }
        case "GET_STATE_WIN_REPLY":
            return { ...state, data: state }
        default:
            return state
    }
}

