import axios from "axios";
import { setHeaders, setHeaderSendFile } from "../controllers/option.controller";

const ROUTE_PROFILE = `${process.env.REACT_APP_URL_API}/profile`

//* отправка запроса - получение данных профиля
export const getDataProfile = async () => {
    const url = `${ROUTE_PROFILE}`
    const option = {
        headers: setHeaders(false),
        url
    }
    try {
        const response = await axios(option)
        if (response.data?.data === undefined) {
            throw new Error('Property `data` not found')
        }
        return response.data.data
    } catch (error) {
        console.log(error)
        return null
    }
}

//* отпрвка заспроса - на получение картинки для сторис
export const sendBotStory = async (photo) => {
    try {
        const imageBlob = await new Promise(resolve => photo.toBlob(resolve, 'image/jpeg'));
        const formData = new FormData();
        formData.append("photo", imageBlob, "photo.jpeg");

        const response = await axios.post(`${ROUTE_PROFILE}/bot/send/story`, formData, {
            headers: setHeaderSendFile
        })

        if (response.data?.message === undefined) {
            throw new Error('Property `message` not found')
        }

        return response.data
    } catch (error) {
        console.log(error)
        return null
    }
}