import axios from "axios";
import { setHeaders } from "../controllers/option.controller";

const ROUTE = `${process.env.REACT_APP_URL_API}/question`
const ROUTE_STOPLIST = `${process.env.REACT_APP_URL_API}/stoplist`

//* отправка запроса - получение вопросов и ответов
export const getQuestions = async () => {
    const url = `${ROUTE}`
    const option = {
        headers: setHeaders(false),
        url
    }
    try {
        const response = await axios(option)
        if (response.data?.data === undefined) {
            throw new Error('Property `data` not found')
        }
        return response.data.data
    } catch (error) {
        console.log(error)
        return null
    }
}

//* отправка запроса - удалить или восстановить вопрос
export const questionToggleDelete = async (questionId, setIsDelete, isDelete) => {
    const url = `${ROUTE}/${questionId}`
    const option = {
        method: !isDelete ? 'delete' : 'put',
        headers: setHeaders(),
        url
    }
    try {
        await axios(option)
        setIsDelete(!isDelete ? true : false)
    } catch (error) {
        console.log(error)
    }
}

//* отправка запроса - поставить лайк и убарть лайк
export const questionToggleLike = async (questionId, isLiked, setLike, setCountLikes) => {
    const url = `${ROUTE}/${questionId}/${!isLiked ? 'like' : 'unlike'}`
    const option = {
        method: 'post',
        headers: setHeaders(),
        url
    }

    try {
        const response = await axios(option)
        if (response?.data === undefined) {
            throw new Error("Property is not defined")
        }

        setLike(!isLiked ? true : false)
        setCountLikes(response.data.data.likes)
    } catch (error) {
        console.log(error)
    }
}


//* отправка запроса - ответить на вопрос
export const replyOnQuestion = async (questionId, reply) => {
    const url = `${ROUTE}/${questionId}`
    const option = {
        method: 'patch',
        headers: setHeaders(true),
        data: JSON.stringify({ answer: reply }),
        url
    }

    try {
        axios(option)
    } catch (error) {
        console.log(error)
    }
}

//* отправка запроса - заблокировать пользователя
export const userToggleBan = async (questionId, isBan, setIsBan) => {
    const url = `${ROUTE_STOPLIST}/${questionId}`
    const option = {
        method: !isBan ? "post" : "delete",
        headers: setHeaders(false),
        url
    }
    try {
        const response = await axios(option)
        if (response.data?.success === undefined) {
            throw new Error('Property `success` not found')
        }
        setIsBan(!isBan ? true : false);
        return response.data.success
    } catch (error) {
        console.log(error)
        return null
    }
}