import React, { useEffect, useRef, useState } from "react"
import { useDispatch, useSelector } from "react-redux";
import {ReactComponent as IconClose } from '../../Question/QuestionInput/Reply/icon/Icon.svg'
import imgBack from './img/story-main-source.svg'
import {ReactComponent as DownloadStoryIcon} from './icon/downloadStory.svg'
import {ReactComponent as CopyLinkIcon} from './icon/copyLink.svg'
import './style.css'
import { sendBotStory } from "../../../actions/profile";
import { Loader } from "../../Loader/Loader";
import CopyToClipboard from "react-copy-to-clipboard";

class Canvas extends React.Component {
    state = {
        width: this.props.width,
        height: this.props.height,
        x: this.props.x,
        y: this.props.y,
        isVisible: this.props.isVisible,

    }
    componentDidMount() {
      const canvas = this.refs.canvas;
      const ctx = canvas.getContext("2d");
      const img = this.refs.image;
  
      img.onload = () => {
        ctx.drawImage(img, 0, 0, this.state.width, this.state.height);
        ctx.fillStyle = 'white'
        ctx.font = this.state.isVisible ? "normal 700 8px Nunito" : "normal 700 64px Nunito";
        ctx.fillText(this.props.text, this.state.x, this.state.y); // THIS IS THE PLACE TEXT IS EMBEDDED INTO THE PICTURE
      };

      if (!this.state.isVisible) this.props.setCanvasRef(this.refs.canvas);
    }
    render() {
      return (
        <>
          <canvas 
                id={this.state.isVisible ? "no-download" : 'download-story'}
                ref="canvas" 
                width={this.state.width} 
                height={this.state.height}
                style={{
                    borderRadius: '16px', 
                    position: this.state.isVisible ? 'initial' : 'fixed',
                    visibility: this.state.isVisible ? 'visible' : 'hidden'
                }}
          />
          <img
            ref="image"
            alt=""
            src={imgBack}
            width={this.state.width}
            height={this.state.height}
            style={{display: 'none'}}
          />
        </>
      );
    }
}

export const ShareProfile = () => {
    const dispatch = useDispatch()
    const [copyLink, setCopyLink] = useState("");
    const [isSendStory, setIsSendStory] = useState(null);
    const [canvasDownload, setCanvasRef] = useState()
    const shareWindowData = useSelector(state => state.editProfile.data)

    const handleCloseWindow = () => {
        dispatch({ type: "SET_STATE_EDIT_PROFILE", data: {
            isOpen: false
        }})
    }

    const handleDownloadImage = async () => {
        try {
            if (canvasDownload) {
                setIsSendStory(false);
                const isSend = await sendBotStory(canvasDownload)
                if (isSend) setIsSendStory(isSend?.success);
            }
        } catch (error) {
            console.log(error)
        }
    }

    const linkMe = `ama.fan/${shareWindowData.screenName}`;

    return (
        shareWindowData?.isOpen &&
        <div id="reply-question-container">
            <div id="reply-close-container">
                <IconClose onClick={() => handleCloseWindow()} />
            </div>
            <div id="reply-text-container">
                <div id="share-photo-storis">
                    {/* image preview */}
                    {window?.innerHeight >= 518 && 
                        <Canvas text={linkMe} 
                            width={136} 
                            height={236} 
                            x={22} y={185}
                            isVisible={true}
                            setCanvasRef={setCanvasRef} 
                        />
                    }
                    {/* image download */}
                    <Canvas text={linkMe} 
                            width={980} 
                            height={1743} 
                            x={150} y={1370}
                            isVisible={false}
                            setCanvasRef={setCanvasRef} 
                    />
                </div>
                <div id="button-share-link-and-save">
                    <h2>Поделиться</h2>
                    <div id="share-link-and-save-button-container">
                        <div className="share-link-and-save" onClick={handleDownloadImage}>
                            <span>Сохранить на диск</span>
                            {/* <span>{window?.innerHeight >= 518 && "Получить изображение для историй"}</span> */}
                            {isSendStory === null ? <DownloadStoryIcon /> : (
                                    (!isSendStory) 
                                    ? 
                                    <div id="send-story-loader"><Loader/></div> 
                                    : 
                                    <DownloadStoryIcon />
                                )
                            }
                        </div>
                        <CopyToClipboard text={`https://ama.fan/${shareWindowData.screenName}`}
                                onCopy={() => setCopyLink({copied: true})}>
                                <div className="share-link-and-save">
                                    <span>Копировать ссылку</span>
                                    <div id={copyLink.copied && "copy-link"}><CopyLinkIcon /></div>
                                </div>
                        </CopyToClipboard>
                    </div>
                </div>
            </div>
        </div>
    )
}