import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ReactComponent as IconClose } from './icon/Icon.svg'
import { replyOnQuestion } from '../../../../actions/question'
import './style.css'

export const Reply = () => {
    const [reply, setReply] = useState("")
    const [warn, setWarn] = useState(false)
    const dispatch = useDispatch()
    const questionData = useSelector(state => state.questionData.data)
    console.log(questionData)

    const stateReply = useSelector(state => state.winReply.data)
    // console.log(stateReply)

    const handleEnterReply = (event) => {
        if (reply.length > 1) {
            setWarn(false)
        } else {
            setWarn(true)
        }
        setReply(event.target.value)
    }

    const handleCloseWindow = () => {
        dispatch({
            type: "SET_STATE_WIN_REPLY", data: {
                isOpen: false,
                questionId: stateReply.questionId,
                text: stateReply.text,
                index: stateReply.index
            }
        })
        setReply("")
    }

    const handleDelete = () => {
        dispatch({type: "DELETE_QUESTIONS_INPUT", id: stateReply.questionId})
        handleCloseWindow();
    }

    //refactoring
    const handleSendReply = async () => {
        const questionId = stateReply?.questionId || null

        if (reply !== "" && questionId !== null) {
           replyOnQuestion(questionId, reply)
           handleDelete()
        }
        setWarn(true)
    }

    return (
        stateReply.isOpen &&
        <div id="reply-question-container">
            <div id="reply-close-container">
                <IconClose onClick={() => handleCloseWindow()} />
            </div>
            <div id="reply-text-container">
                <h2>{stateReply.text}</h2>
                {warn && <span>Вы не можете отправлять пустой вопрос.</span>}
                <input type="text" value={reply} onChange={handleEnterReply}
                />
            </div>
            <button onClick={handleSendReply}>Опубликовать</button>
        </div>
    )
}