import { useEffect, useState } from "react"
import { getDataProfile } from "../../actions/profile"
import { BottomMenu } from "../BottomMenu/bottom-menu"
import { Loader } from "../Loader/Loader"
import { QuestionEmpty } from "../Question/QuestionEmpty"
import { QuestionOutput } from "../Question/QuestionOutput/QestionOutput"
import { HeaderProfile } from "./HeaderProfile/HeaderProfile"
import { ReactComponent as MoreQuestions } from '../../components/Question/icon/Input.svg'
export const Profile = () => {
    const [profile, setProfile] = useState({})
    const [isLoader, setIsLoader] = useState(false)

    const handleGetDataProfile = async () => {
        try {
            const data = await getDataProfile();
            if (data) {
                setProfile(data)
                setIsLoader(true)
                console.log(data)
            }
        } catch (error) {
            console.log(error)
        }
    }

    useEffect(() => {
        handleGetDataProfile()
    }, [])

    const lengthItems = profile.questions?.items.length || 0;

    if (isLoader) {
        return (
            <div className="main-page-container">
                <HeaderProfile user={profile.user} itemsLen={lengthItems}/>
                {lengthItems > 0
                    ? 
                    (<QuestionOutput question={profile.questions} isProfile={true} />) 
                    : 
                    (<QuestionEmpty 
                            Icon={MoreQuestions} 
                            title={"Позови друзей!"} 
                            description={"Чтобы получить первые вопросы, расскажи друзьям о своём профиле"} 
                    />)
                }
                <BottomMenu active={1} />
            </div>
        )
    }

    return (
        <div className="App" style={{ alignItems: 'center', justifyContent: 'center' }}>
            <Loader />
        </div>
    )
}